.header {
	display: flex;
	align-items: center;
	justify-content: space-between;

	height: 50px;
	padding: 0;
	color: #686869;
	background-color: #333336;
}
.logo {
	width: 35px;
	height: 35px;
	background-color: inherit;
}
.logoBlock {
	margin-left: 23%;
	background-color: inherit;
}
.logoBlock span {
	margin-left: 10px;
	background-color: inherit;
	font-weight: bold;
	color: #ffffff;
	text-shadow: 0px 1px 0 rgb(NaN, 192, 192), 0px -1px 0 rgb(NaN, 192, 192),
		1px 0px 0 rgb(NaN, 192, 192), -1px 0px 0 rgb(NaN, 192, 192),
		1px 1px 0 rgb(NaN, 192, 192), 1px -1px 0 rgb(NaN, 192, 192),
		-1px 1px 0 rgb(NaN, 192, 192), -1px -1px 0 rgb(NaN, 192, 192),
		0px 2px 0 rgb(NaN, 192, 192), 0px -2px 0 rgb(NaN, 192, 192),
		2px 0px 0 rgb(NaN, 192, 192), -2px 0px 0 rgb(NaN, 192, 192),
		2px 2px 0 rgb(NaN, 192, 192), 2px -2px 0 rgb(NaN, 192, 192),
		-2px 2px 0 rgb(NaN, 192, 192), -2px -2px 0 rgb(NaN, 192, 192),
		0px 3px 0 rgb(NaN, 192, 192), 0px -3px 0 rgb(NaN, 192, 192),
		3px 0px 0 rgb(NaN, 192, 192), -3px 0px 0 rgb(NaN, 192, 192),
		3px 3px 0 rgb(NaN, 192, 192), 3px -3px 0 rgb(NaN, 192, 192),
		-3px 3px 0 rgb(NaN, 192, 192), -3px -3px 0 rgb(NaN, 192, 192);
	-webkit-text-shadow: 0px 1px 0 rgb(NaN, 192, 192),
		0px -1px 0 rgb(NaN, 192, 192), 1px 0px 0 rgb(NaN, 192, 192),
		-1px 0px 0 rgb(NaN, 192, 192), 1px 1px 0 rgb(NaN, 192, 192),
		1px -1px 0 rgb(NaN, 192, 192), -1px 1px 0 rgb(NaN, 192, 192),
		-1px -1px 0 rgb(NaN, 192, 192), 0px 2px 0 rgb(NaN, 192, 192),
		0px -2px 0 rgb(NaN, 192, 192), 2px 0px 0 rgb(NaN, 192, 192),
		-2px 0px 0 rgb(NaN, 192, 192), 2px 2px 0 rgb(NaN, 192, 192),
		2px -2px 0 rgb(NaN, 192, 192), -2px 2px 0 rgb(NaN, 192, 192),
		-2px -2px 0 rgb(NaN, 192, 192), 0px 3px 0 rgb(NaN, 192, 192),
		0px -3px 0 rgb(NaN, 192, 192), 3px 0px 0 rgb(NaN, 192, 192),
		-3px 0px 0 rgb(NaN, 192, 192), 3px 3px 0 rgb(NaN, 192, 192),
		3px -3px 0 rgb(NaN, 192, 192), -3px 3px 0 rgb(NaN, 192, 192),
		-3px -3px 0 rgb(NaN, 192, 192);
	-moz-text-shadow: 0px 1px 0 rgb(NaN, 192, 192), 0px -1px 0 rgb(NaN, 192, 192),
		1px 0px 0 rgb(NaN, 192, 192), -1px 0px 0 rgb(NaN, 192, 192),
		1px 1px 0 rgb(NaN, 192, 192), 1px -1px 0 rgb(NaN, 192, 192),
		-1px 1px 0 rgb(NaN, 192, 192), -1px -1px 0 rgb(NaN, 192, 192),
		0px 2px 0 rgb(NaN, 192, 192), 0px -2px 0 rgb(NaN, 192, 192),
		2px 0px 0 rgb(NaN, 192, 192), -2px 0px 0 rgb(NaN, 192, 192),
		2px 2px 0 rgb(NaN, 192, 192), 2px -2px 0 rgb(NaN, 192, 192),
		-2px 2px 0 rgb(NaN, 192, 192), -2px -2px 0 rgb(NaN, 192, 192),
		0px 3px 0 rgb(NaN, 192, 192), 0px -3px 0 rgb(NaN, 192, 192),
		3px 0px 0 rgb(NaN, 192, 192), -3px 0px 0 rgb(NaN, 192, 192),
		3px 3px 0 rgb(NaN, 192, 192), 3px -3px 0 rgb(NaN, 192, 192),
		-3px 3px 0 rgb(NaN, 192, 192), -3px -3px 0 rgb(NaN, 192, 192);
	font-size: 38px;
	color: rgb(255, 255, 255);
	font-family: Verdana, Geneva, sans-serif;
}
.time {
	background: inherit;
	margin-right: 20px;
}
.loginName {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	background: inherit;
	margin-right: 15px;
	color: #ffffff;
	font-size: 20px;
}
.loginName a {
	background: inherit;
	text-decoration: none;
	color: #ffffff;
}
.loginName a:hover {
	background: inherit;
	text-decoration: none;
	color: #ffffff;
}
.loginName img {
	margin-right: 10px;
	width: 30px;
	border-radius: 50px;
}
